<template>
  <v-container fluid>
    <v-app-bar app color="primary" dark prominent>
      <v-btn
        @click="closethis()"
        absolute
        dark
        fab
        bottom
        right
        :small="$vuetify.breakpoint.smAndDown ? true: false"
        color="red"
      >
        <v-icon>mdi-close-circle</v-icon>
      </v-btn>
      <v-layout text-center wrap>
        <v-flex xs12 sm12 md3 lg3 xl3>
          <div :class=" $vuetify.breakpoint.smAndDown ? 'caption': 'headline'">{{opts.nomeCliente}}</div>
        </v-flex>
        <v-flex xs6 sm6 md3 lg3 xl3>
          <v-text-field
            class="black--text"
            dense
            outlined
            shaped
            readonly
            rounded
            v-model="opts.CpfCedente"
            placeholder
            label="Cedente"
            v-mask="mask"
            required
          >
            <v-btn
              slot="append"
              @click="doCopy(opts.CpfCedente)"
              :small="$vuetify.breakpoint.smAndDown ? true: false"
              :class="classBtn"
              color="green"
            >
              <v-icon left>mdi-content-copy</v-icon>
              {{$vuetify.breakpoint.smAndDown ? '': 'Copiar'}}
            </v-btn>
          </v-text-field>
        </v-flex>
        <v-flex xs6 sm6 md3 lg3 xl3>
          <v-text-field
            class="black--text"
            dense
            outlined
            shaped
            readonly
            rounded
            v-model="opts.CpfSacado"
            placeholder
            label="CNPJ"
            v-mask="mask"
            required
          >
            <v-btn
              slot="append"
              @click="doCopy(opts.CpfSacado)"
              small
              :class="classBtn"
              color="green"
            >
              <v-icon left>mdi-content-copy</v-icon>
              {{$vuetify.breakpoint.smAndDown ? '': 'Copiar'}}
            </v-btn>
          </v-text-field>
        </v-flex>

        <v-flex xs12 sm12 md3 lg3 xl3>
          <v-text-field
            class="black--text"
            dense
            dark
            outlined
            shaped
            readonly
            rounded
            v-model="opts.NossoNum"
            placeholder
            label="Nosso Numero"
            required
          >
            <v-btn
              slot="append"
              @click="doCopy(opts.NossoNum)"
              small
              :class="classBtn"
              color="green"
            >
              <v-icon left>mdi-content-copy</v-icon>Copiar
            </v-btn>
          </v-text-field>
        </v-flex>
      </v-layout>
    </v-app-bar>

    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <v-layout text-center wrap>
      <v-flex xs12>
        <div v-if="!bancoCuzao" class="resp-container">
          <iframe
            v-if="iframe.loaded"
            allowfullscreen
            class="resp-iframe"
            :src="iframe.src"
            v-on:load="load()"
          ></iframe>
        </div>
        <v-card shaped elevation="24" v-if="bancoCuzao" class="mx-auto" max-width="344">
          <v-card-text>
            <div></div>
            <p class="display-1 text--primary">2-Via de Boleto</p>
            <div class="text--primary">
              Você será redirecionado para o sistem do banco {{opts.banco.trim()}}
              <br />
              {{`${opts.agencia.trim()}${opts.conta.trim()}${opts.NossoNum.trim()}`}}
            </div>
          </v-card-text>
          <v-card-actions>
            <v-btn
              v-if="havedoCopy"
              @click="abrirBancoCuzao()"
              text
              color="deep-purple accent-4"
            >Acessar Banco</v-btn>
            <v-btn @click="doItauCopy(opts)" text color="deep-purple accent-4">Copiar Chave</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mask } from "vue-the-mask";
export default {
  name: "BoletoBanco",
  directives: {
    mask
  },
  props: ["opts"],
  mounted() {
    this.defMainColor();
    this.setupThis();
  },
  data: () => ({
    overlay: false,
    mask: "##.###.###/####-##",
    bancoCuzao: false,
    havedoCopy: false,
    urlBancoCuzao: "",
    frame: {},
    iframe: {
      src: "",
      loaded: false
    },
    mainColor: "primary",
    classBtn: "white--text"
  }),
  methods: {
    doItauCopy: function(opts) {
      let copyText = `${opts.agencia.trim()}${opts.conta.trim()}${opts.NossoNum.trim()}`;
      this.$copyText(copyText);
      this.havedoCopy = true;
    },
    doCopy: function(texto) {
      let textCopy = texto
        .replace(".", "")
        .replace(".", "")
        .replace("/", "")
        .replace("-", "");
      this.$copyText(textCopy);
    },
    defMainColor: async function() {
      if (this.opts.banco === "bradesco") {
        this.mainColor = "#cc092f";
        this.classBtn = "white--text";
      } else if (this.opts.banco === "safra") {
        this.mainColor = "#151e45";
        this.classBtn = "white--text";
      } else if (this.opts.banco === "itau") {
        this.mainColor = "#ec7000";
        this.classBtn = "white--text";
      } else if (this.opts.banco === "brasil") {
        this.mainColor = "#f8d117";
        this.classBtn = "white--text";
      }
    },
    setupThis() {
      this.overlay = true;
      if (this.opts.banco === "bradesco") {
        this.iframe.src =
          "https://www.ib12.bradesco.com.br/ibpfsegundaviaboleto/segundaViaBoletoPesquisarCPFCNPJ.do";
        this.iframe.loaded = true;
      } else if (this.opts.banco === "safra") {
        this.iframe.src =
          "https://wwws.safraempresas.com.br/pjseguro/SacadosConsultaBoletoCobr_NCS.asp?origem=C&stpf=1";
        this.iframe.loaded = true;
      } else if (this.opts.banco === "itau") {
        if (this.opts.vencido) {
          this.urlBancoCuzao =
            "https://www.itau.com.br/servicos/boletos/atualizar/";
        } else {
          this.urlBancoCuzao =
            "https://www.itau.com.br/servicos/boletos/segunda-via/";
        }
        this.iframe.src = "";
        this.iframe.loaded = false;
        this.bancoCuzao = true;
        this.overlay = false;
      } else if (this.opts.banco === "brasil") {
        this.iframe.src =
          "https://www63.bb.com.br/portalbb/boleto/boletos/hc21e,802,3322,10343.bbx";
        this.iframe.loaded = true;
      }
    },
    abrirBancoCuzao() {
      window.open(this.urlBancoCuzao, "_blank", "width=900,height=auto");
    },
    closethis() {
      this.$emit("close", "");
    },
    load() {
      this.overlay = false;
    }
  }
};
</script>

<style>
.resp-container {
  position: relative;
  overflow: hidden;
  padding-top: 200%;
}
.resp-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
</style>